import React, { Component } from 'react'
import {API_BASE_URL} from './APIHelper';

export default class Viewer extends Component {

    state={
        object:[]
    }

    componentWillMount(){
        this.setState({
            object:this.props.location.state
        })
  
}
    render() {
        const {object} =this.state.object
        return (
            <div className="container">
                <img className="card-img-top" src={`${API_BASE_URL}/showImage?farm=${this.state.object.farm}&server=${this.state.object.server}&id=${this.state.object.id}&secret=${this.state.object.secret}`} alt="Card image" height='500px' />
                <h2>{this.state.object.id}</h2>
                <p>{this.state.object.title}</p>
                <a className="btn btn-primary" href={`${API_BASE_URL}/downloadImage?farm=${this.state.object.farm}&server=${this.state.object.server}&id=${this.state.object.id}&secret=${this.state.object.secret}`}>Download image</a>
            </div>
        )
    }
}
