import axios from 'axios';

export const API_BASE_URL='http://localhost:8050/image'; /*'&tags=Animal&per_page=10&page=1'*/

const request = (options) => {
    
    if (options.method==='GET') {
        return axios.get(options.url);    
    }
    else if (options.method==='POST') {
        return axios.post(options.url, options.body);
    }
    else if(options.method==='PUT'){
        return axios.put(options.url, options.body);
    }else {
        return axios.delete(options.url)
    }
};


export function getData(activePage, pageRangeDisplayed, search) {
    return request({
        url: API_BASE_URL+'/getImages'+'?tags='+search+'&limit='+pageRangeDisplayed+'&page='+activePage,
        method: 'GET'
    });
}