import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {getData, API_BASE_URL} from './APIHelper';


class GetMapping extends Component{

        state = {
            photos: [],
            search:'',
            types:'',
            activePage:1,
            itemCounterPage: 0,
            totalItemsCount: 0,
            pageRangedDisplayed: 12
        }

    componentWillMount(){
       this.getProduct()
    }

    getProduct= ()=>{
        if (this.state.types==='' | this.state.types.trim()==='') {
            const a='Animal';
            getData(this.state.activePage, this.state.pageRangedDisplayed, a).then(res =>{
                this.setState({
                    itemCounterPage: res.data.photos.pages,
                    totalItemCount: res.data.photos.total,
                    photos:res.data.photos.photo,
                })
            })        
        }
        else {
            getData(this.state.activePage, this.state.pageRangedDisplayed, this.state.types).then(res =>{
            this.setState({
                itemCounterPage: res.data.photos.pages,
                totalItemCount: res.data.photos.total,
                photos:res.data.photos.photo,
            })
            })
        }
    }
    

    updateSeach =(e)=>{
        this.setState({search: e.target.value.substr(0,10)});
    }
    updateSeachType =(e)=>{
        this.setState({types: e.target.value.substr(0,30)});
    }

    handlePageChange =()=>{
        if (this.state.activePage<this.state.itemCounterPage) {
        let count= this.state.activePage+1;
        this.setState({
            activePage: count
        });
        this.getProduct();
    }
    }  
    handlePageChanger=()=>{
        if (this.state.activePage>1) {
        let count= this.state.activePage-1;
        this.setState({
            activePage: count
        });
        this.getProduct();
        }
    }

    render(){
        const photoFilter = this.state.photos.filter( photo =>{
            return photo.title.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1;
        })
        return(
            
            <div className="container">
                <div>
                    <div className="input-group">
                        <div className="input-group-prepend">
                            <button className="btn btn-outline-secondary" style={{height: '38px'}} type="button" onClick={this.getProduct}>Fint it</button>
                        </div>
                        <input className="form-control mb-1" type="test" name="find other types" value={this.state.types} onChange={this.updateSeachType} placeholder="Animal is a Default or blank" />
                        <input className="form-control mb-3" type="test" name="search" value={this.state.search} onChange={this.updateSeach} placeholder="Searching only title" />
                        
                    </div>
                    <div className="form-group">
                    <div className="input-group-prepend">
                        <p className="form-control">Topilganlari</p>
                        <p className="form-control">{this.state.totalItemCount}</p>
                        <p className="form-control">Oxirgi page</p>
                        <p className="form-control">{this.state.itemCounterPage}</p></div>
                    </div>
                        {
                            photoFilter.map(
                                data => {
                                    return(
                                    <div key={data.id} className="container" style={{display: 'inline-block', width: '340px', marginLeft: '5px', marginBottom: '3px'}}>
                                        <div className="card" style={{width: "300px", height: '420px'}}>
                                            <img className="card-img-top" src={`${API_BASE_URL}/showImage?farm=${data.farm}&server=${data.server}&id=${data.id}&secret=${data.secret}`} alt="Card image" height="200px" />
                                            <div className="card-body">
                                                <h4 className="card-title">ID: {data.id}</h4>
                                                <p className="card-text">Title: {data.title}</p>
                                                <Link className="btn btn-primary" to={{pathname:"/viewer", state: data}} >See profil</Link>
                                                <a className="btn btn-primary" href={`${API_BASE_URL}/downloadImage?farm=${data.farm}&server=${data.server}&id=${data.id}&secret=${data.secret}`}>Download image</a>
                                            </div>
                                        </div>
                                    </div>
                                    )
                            })
                        }
<div>
  <ul className="pagination">
    <li className="page-item"><button className="page-link" onClick={this.handlePageChanger}> Previous</button></li>
    <li className="page-link">{this.state.activePage}</li>
    <li className="page-item"><button className="page-link" onClick={this.handlePageChange}>Next</button></li>
  </ul>
</div>
                </div>
            </div>
        )
    }

}

export default GetMapping;